<template>
  <div class="container">
    <form id="projectForm" @submit.prevent="submitForm">
      <h2>IoT Project Requirements Form</h2>
      <label for="name">Full Name:</label>
      <input type="text" id="name" name="name" required />

      <label for="email">Email Address:</label>
      <input type="email" id="email" name="email" required />

      <label for="phone">Phone Number:</label>
      <input type="text" id="phone" name="phone" required />

      <label for="projectName">Project Name:</label>
      <input type="text" id="projectName" name="projectName" required />

      <label for="projectDescription">Project Description:</label>
      <textarea
        id="projectDescription"
        name="projectDescription"
        maxlength="1005"
        rows="5"
        required
        class="fixed-textarea"
      ></textarea>

      <label for="technologies">Technologies Required:</label>
      <textarea
        id="technologies"
        name="technologies"
        rows="3"
        required
        class="fixed-textarea"
      ></textarea>

      <label id="timeline" for="timeline">Estimated Project Timeline:</label>
      <input type="text" id="timeline" name="timeline" required />
      <input type="hidden" id="timelineStart" name="timelineStart" />
      <input type="hidden" id="timelineEnd" name="timelineEnd" />
      <div class="date-info" id="dateInfo"></div>
      <input type="submit" id="submitButton" value="Submit" />
    </form>
  </div>
</template>

<script>
import projectFormPDF from "@/scripts/script.js";
import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.css";

export default {
  name: "HomeView",
  mounted() {
    projectFormPDF();

    const timelineInput = document.getElementById("timeline");
    const timelineStartInput = document.getElementById("timelineStart");
    const timelineEndInput = document.getElementById("timelineEnd");
    const dateInfo = document.getElementById("dateInfo");

    flatpickr("#timeline", {
      mode: "range",
      dateFormat: "Y-m-d",
      clickOpens: true, // Add this option
      onChange: function (selectedDates, dateStr) {
        if (selectedDates.length === 2) {
          const startDate = selectedDates[0];
          const endDate = selectedDates[1];
          const duration = Math.ceil(
            (endDate - startDate) / (1000 * 60 * 60 * 24)
          );
          timelineStartInput.value = startDate.toISOString().split("T")[0];
          timelineEndInput.value = endDate.toISOString().split("T")[0];
          dateInfo.textContent = `Selected Range: ${dateStr} (${duration} days)`;
        } else {
          dateInfo.textContent = "";
        }
      },
    });

    timelineInput.addEventListener("timeline", function () {
      const days = parseInt(this.value);
      if (!isNaN(days)) {
        const startDate = new Date(timelineInput.value);
        const endDate = new Date(
          startDate.getTime() + days * (1000 * 60 * 60 * 24)
        );
        const endDateStr = endDate.toISOString().split("T")[0];
        timelineEndInput.value = endDateStr;
        dateInfo.textContent = `Selected Range: ${timelineInput.value} to ${endDateStr} (${days} days)`;
      }
    });
  },
  methods: {
    submitForm() {
      // Handle form submission here
      // You can access form data using Vue's data properties
    },
  },
};
</script>

<style scoped>
/* Import the style.css file from the new location */
@import "@/styles/style.css";
</style>
