/* eslint-disable prettier/prettier */
// convert html to pdf
import html2pdf from "html2pdf.js";
import FuturIsTechLogo from "@/assets/logo.png";

export default function projectFormPDF() {
  const form = document.getElementById("projectForm");
  const submitButton = document.getElementById("submitButton");

  form.addEventListener("submit", function (event) {
    event.preventDefault();
    var projectName = document.getElementById("projectName").value;
    submitButton.style.display = "none";
    
    const logoImg = document.createElement("img");
    logoImg.src = FuturIsTechLogo;
    logoImg.style.width = "200px";
    logoImg.style.position = "relative";
    logoImg.style.bottom = "0px"; // Adjust bottom position as needed
    logoImg.style.left = "420px"; // Adjust right position as needed
    form.appendChild(logoImg);

    html2pdf()
      .set({
        margin: 1,
        filename: projectName + ".pdf",
        image: { type: "jpeg", quality: 0.1 },
        html2canvas: { scale: 10 },
        jsPDF: { unit: "in", format: "letter", orientation: "portrait" },
      })
      .from(form)
      .save()
      .then(() => {
        form.removeChild(logoImg);
        submitButton.style.display = "block";
      });
  });
}
